.root {
  height: auto;
  width: auto;
  border-radius: 8px;
  display: flex;
  column-gap: 8px;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 4px 16px;
}

.gameNameContainer {
  width: auto;
  height: 100%;
  align-items: center;
  display: flex;
  max-height: 40px;

  .gameName {
    font-family: 'Lobster', sans-serif;
    letter-spacing: 4px;
  }
}

.resultsWrapper {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.result {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  height: 40px;
  width: 40px;
  background-color: white;
  color: black;
  text-align: center;
  border-radius: 8px;
}

.brainIcon {
  height: 14px;
  width: 14px;
  vertical-align: top;
}

@media only screen and (max-width: 768px) {
  .root {
    border-radius: 8px;
    column-gap: 6px;
    padding: 4px 8px;
  }
  .resultsWrapper {
    gap: 6px;
  }
  .result {
    height: 25px;
    width: 25px;
    gap: 6px;
  }
}
